.header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	background-color: #1c1c21;
	padding-left: 130px;
	padding-right: 140px;

	.last-menu {
		width: 344px;
		display: flex;
		justify-content: right;
	}
}

ul.menu {
	display: flex;
	padding-left: 0;
	margin: 0;
	> li {
		display: flex;
		align-items: center;
		list-style: none;
	}
}

.link {
	text-decoration: none;
	display: flex;
	align-items: center;
	list-style: none;
}
